<template>
  <question v-on:close-modal="closeModal" v-on:choose-location="view" v-if="question"/>
  <location-form v-on:close-modal="closeModal" v-else/>
</template>

<script>
import LocationForm from './components/LocationForm.vue'
import Question from './components/Question.vue'
export default {
  name: 'Location',
  components: {
    LocationForm,
    Question
  },
  data() {
    return {
      question: true
    }
  },
  methods: {
    view() {
      this.question = !this.question
    },
    closeModal() {
      const backdrop = document.querySelector('.backdrop')
      const modals = [...document.querySelectorAll('.modal-wrapper')]
      backdrop.classList.remove('backdrop_opened')
      modals.forEach(element => element.classList.remove('modal-wrapper_opened'))
    }
  }
};
</script>



