import Vue from 'vue'
import Cart from './cart/Cart.vue'
import CategoryButton from './categoryButton/CategoryButton.vue'
import Location from './location/Location.vue'
import store from './store'

Vue.config.productionTip = false

new Vue({
  store,
  components: {
    'location': Location
  }
}).$mount('location')

new Vue({
  store,
  components: {
    'cart': Cart
  }
}).$mount('cart')

const buttons = [...document.querySelectorAll('.category-button')]

buttons.forEach(element => {
  new Vue({
    store,
    render: h => h(CategoryButton, {
      props: {
        dataPrice: Number(element.dataset.price),
        dataItemId: Number(element.dataset.itemId)
      }
    })
  }).$mount(`#${element.id}`)
})

