<template>
  <div class="city-modal__field search-field" :class="{ 'city-modal__field_drop': open  }">
    <input @input="open = true" v-model="field" type="text" :placeholder="placeholder" />
    <ul class="city-list" :class="{ 'city-list_opened': open }">
      <li v-for="(item, index) in inputFiltering" :key="index" @click="setValue(item)" class="city-list__item">{{ item }}</li>
    </ul>
    <span @click.prevent="open = !open" class="fas fa-chevron-down" :class="{ up:open }"></span>
  </div>
</template>

<script>
export default {
  name: "Field",
  props: {
    list: {
      type: Array,
      required: true,
      default: []
    },
    placeholder: {
      type: String,
      required: true,
      default: ''
    }
  },
  data() {
    return {
      open: false,
      field: ''
    }
  },
  computed: {
    inputFiltering() {
      const regexp = new RegExp(this.field, 'i')
      return [...this.list.filter(item => regexp.test(item))]
    }
  },
  methods: {
    setValue (val) {
      this.field = val
      this.open = false
    }
  }
};
</script>

