  display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    width: 324px;
    height: 30px;
    margin-bottom: 21px;
    border: 1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    background-color: #fff;
    overflow: hidden;

    input {
      position: relative;
      width: 100%;
      height: 100%;
      padding: 0 21px;
      margin-right: -25px;
      line-height: 30px;
      color: #515151;
      font-size: 16px;
      border: none;
      outline: none;
      z-index: 1;
    }

    span {
      position: relative;
      z-index: 3;
      margin-right: 11px;
      color: #4f4f4f;
      font-size: 16px;
      cursor: pointer;
      transition: all ease 0.5s;
    }

    &_drop {
      overflow: visible;
    }
}

.city-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  list-style: none;
  padding: 7px 11px;
  margin: 0;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  border-radius: 0 0 3px 3px;
  border: 1px solid #ffffff;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  transition: all ease 0.3s;

  &__item {
    margin-bottom: 9px;
    color: #515151;
    font-size: 16px;
    cursor: pointer;
  }

  &_opened {
    opacity: 1;
    visibility: visible;
    z-index: 5;
  }
}

.up {
  transform: rotate(180deg)
}
</style>


